import three from '@/static/img/information3x.png'
import step from '@/components/step/step'
import wPicker from "@/components/w-picker/w-picker.vue";
import {
	mapState
} from 'vuex'
export default {
	data() {
		return {
			value1: '第一步',
			active: 'step-active',
			times: true,
			day: '1970-01-01',
			dayIs: false,
			src: three,
			inputs: '',
			img: '',
			userCode: '',
			name: '',
		};
	},
	onLoad(option) {
		this.userCode = option.userCode;
	},
	methods: {
		all() {
			const that = this;
			// if(){
			// 	
			// }
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'],
				success: function(res) {
					console.log(res)
					that.src = res.tempFilePaths[0];
					that.img = res.tempFilePaths[0];
				}
			})
		},
		commit() {
			var $api = "http://dev.pjy.name:8180/api/bizcust/";
			if (this.img !== '' && this.img !== null) {
				if (this.dayIs) {
					if (this.userCode == 1) {
						this.name = 'buy_auth_photo'
						uni.uploadFile({//$api+
							url: 'user/oil_authorize',
							filePath: this.img,
							name: this.name,
							header: {
								Token: this.Token
							},
							formData: {
								"roles": this.userCode,
								"buy_auth_exp": this.day,
							},
							success: function(res) {
								console.log(res)
								var data = JSON.parse(res.data)
								if (res.statusCode == 200 && data.errorCode == 0) {
									uni.showToast({
										title: '申请成功，待管理员确认',
										icon: 'none',
									})
									setTimeout(function() {
											uni.hideToast(); 
										uni.switchTab({
											url: '../../info'
										})
									}, 1500)
								} else {
									uni.showToast({
										title: res.message,
										icon: 'none'
									})
								}
							},
						})
					} else if (this.userCode == 2) {
						this.name = 'get_auth_photo'
						uni.uploadFile({
							url: $api+'user/oil_authorize',
							filePath: this.img,
							name: this.name,
							header: {
								Token: this.Token
							},
							formData: {
								"roles": this.userCode,
								"get_auth_exp": this.day,
							},
							success: function(res) {
								console.log(res)
								var data = JSON.parse(res.data)
								if (res.statusCode == 200 && data.errorCode == 0) {
									uni.showToast({
										title: '申请成功，待管理员确认',
										icon: 'none'
									})
									setTimeout(function() {
										uni.hideToast(); 
										uni.switchTab({
											url: '../../info'
										})
									}, 1500)
								} else {
									uni.showToast({
										title: res.message,
										icon: 'none'
									})
								}
							},
						})
					} else {
						this.name = 'bill_auth_photo'
						uni.uploadFile({
							url: $api+'user/oil_authorize',
							filePath: this.img,
							name: this.name,
							header: {
								Token: this.Token
							},
							formData: {
								"roles": this.userCode,
								"bill_auth_exp": this.day,
							},
							success: function(res) {
								var data = JSON.parse(res.data)
								console.log(data)
								if (res.statusCode == 200 && data.errorCode == 0) {
									uni.showToast({
										title: '申请成功，待管理员确认',
										icon: 'none'
									})
									setTimeout(function() {
											uni.hideToast(); 
										uni.switchTab({
											url: '../../info'
										})
									}, 1500)

								} else {
									uni.showToast({
										title: res.message,
										icon: 'none'
									})
								}
							}

						})
					}
				} else {
					uni.showToast({
						title: '请选择授权书的有效时间',
						icon: 'none'
					})
				}
			} else {
				uni.showToast({
					title: '上传图片不能为空',
					icon: 'none'
				})
			}
		},
		changeTimes() {
			this.times = false;
			this.$refs.date.show()
		},
		onConfirm(val) {
			this.day = val.result;
			this.dayIs = true;
		}
	},
	computed: {
		...mapState(['Token'])
	},
	components: {
		wPicker,
		step
	}
}
